* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* common css */
::-webkit-scrollbar{
  width: 4px;
}
::-webkit-scrollbar-thumb{
  background-color: green;
  border-radius: 10px;
}
/* Homepage css */
.homepage-main-body {
  margin-top: 10vh;
}

.homepage-main-image {
  display: block;
  margin: auto;
  height: 90vh;
  /* width: 100vw; */
  background-color: aqua;
  background: url('./components/oil-treatment.jpeg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.homepage-span-diver {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  backdrop-filter: brightness(70%);
  /* height: 100; */
  margin-bottom: 100vh;
}

.homepage-span {
  font-size: 3rem;
  display: block;
  max-width: 1100px;
  margin: auto;
  text-align: center;
  color: white;
  padding: 10px;
  text-shadow: 1px 1px 10px black;
  font-family: "Work Sans", sans-serif;
  /* background-color: rgba(0, 0, 0, 0.27); */
}

.homepage-arrow {
  animation: moveupdown 3s ease-in-out alternate infinite ;
  /* animation-fill-mode: forwards; */
}

.homepage-contentbox {
  margin: 10vh 2vw;
  /* height: 100vh; */
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  background-color: rgba(0, 0, 0, 0.229);
  /* background: url("https://www.google.com/url?sa=i&url=https%3A%2F%2Fwall.alphacoders.com%2Fbig.php%3Fi%3D1343368&psig=AOvVaw0Vf6URIuuynAtz6Qznf8Oi&ust=1715505500701000&source=images&cd=vfe&opi=89978449&ved=0CBIQjRxqFwoTCPCwyJWihYYDFQAAAAAdAAAAABAE"); */
  padding: 10px;
  border-radius: 10px;
}

.homepage-contentbox>img,
.homepage-contentbox>video {
  height: 30vw;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 2px 2px 10px black;
}

.homepage-content-text {
  font-size: 1.5rem;
}

.homepage-content-text>h1 {
  margin-bottom: 10px;
}

.homepage-feedbacks-inner{
  display: flex;
  gap: 40px;
  user-select: none;
  /* flex-wrap: wrap; */
  /* justify-content: space-around;
  align-items: center; */
  margin-bottom: 10vh;
  padding-right: 30px;
  padding-left: 30px;
  padding-bottom: 20px ;
  overflow-x:scroll;
}
.homepage-feedbacks-inner::-webkit-scrollbar{
  background-color: aqua;
  height: 10px;
}
.homepage-feedbacks-inner::-webkit-scrollbar-thumb {
  background-color: orange; 
  border-radius: 5px; /* Border radius of the thumb */
}

.homepage-feedbacks-inner::-webkit-scrollbar-thumb:hover {
  /* Set styles for the thumb when hovered */
  background-color: rgb(255, 192, 74); /* Background color of the thumb on hover */
}

.homepage-feedbacks-inner::-webkit-scrollbar-track {
  /* Set styles for the track (the area where the thumb moves) */
  background-color: #eee; /* Background color of the track */
  border-radius: 5px;
}

.homepage-feedback-card{
  /* flex-grow: 1; */
  min-width: 400px;
  height: 400px;
  /* margin-left:40px; */
  /* max-width: 90vw; */
  padding: 40px;
  box-shadow: 0px 0px 10px black ;
  /* z-index: 10000000; */
  border: 2px solid black;
  transition: all 0.2s ease-in-out;
  background: rgb(255,255,255);
  background: url("./white-abstract-background.avif");
  /* background: radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(216,246,211,1) 63%, rgba(87,245,89,1) 100%); */
}
.homepage-feedback-card-title{
  font-size: 3rem;
  font-style: italic;
  word-wrap: break-word;
  color: rgb(81, 12, 12);
  font-weight: bold;
}
.homepage-feedback-card-rating{
  font-size: 1.5rem;
  color: green;
  font-weight: bold;
}

.homepage-feedback-card-username{
  font-style: oblique;
  color: grey;
}
.homepage-feedback-card-common{
  margin: 15px;

}
@keyframes moveupdown {
  0% {
    color: green;
  }

  50% {
    color: white;
  }

  100% {
    color: orange;
  }
}

@media screen and (max-width:500px) {
  .homepage-span {
    font-size: 2rem;
  }

  .homepage-content-text {
    font-size: 1.1rem;
    text-align: justify;
  }
  .homepage-feedback-card-title{
    font-size: 2rem;
    /* background-color: green; */
  }
  .homepage-feedback-card{
    /* flex-grow: 1; */
    min-width: 350px;
    height: 350px;
    margin-left:40px;
    /* max-width: 90vw; */
    padding: 40px;
    
  }
}

@media screen and (max-width:800px) {
  .homepage-contentbox {
    flex-direction: column;
    text-align: center;
  }

  .homepage-contentbox>img {
    height: 28vh;
    padding: 10px;
    background-color: white;
    border-radius: 10px;
  }
}

/* feedback form css */


.feedback-main-inner-body {
  min-height:100vh;
  /* width: 100vw; */
  padding-top: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(180deg, rgba(149,247,255,1) 0%, rgba(0,212,255,1) 100%);
}

.feedback-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(59, 219, 59);
  border-radius: 10px;
  padding: 20px;
  width: 95vw;
  max-width: 700px;

}
.feedback-form>h1{
  color: white;
}
.feedback-input {
  width: 80vw;
  padding: 5px 20px;
  height: 40px;
  margin: 20px;
  font-size: 1.2rem;
  max-width: 500px;
  border-radius: 10px;
  outline: none;
  border: none;
}

#feedback-textarea {
  /* max-width: 0vw; */
  resize: none;
  height: 20vh;
  padding-top: 20px;
}

.feedback-rating {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 10px 20px;
}

.feedback-rating>label {
  font-size: 1.2rem;
  padding: 10px;
  /* margin-right: 5vw ; */
}

#feedback-stars {
  width: 50%;
  font-size: 1rem;
  text-align: center;
  /* margin-left: 10px; */
  padding: 5px;
  border: none;
  outline: none;
}
.feedback-submit{
  font-size: 1.5rem;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  color: white;
  background-color: orange;
  border: none;
  box-shadow: 1px 1px 10px black;
  cursor: pointer;
}
@media screen and (max-width:800px) {
  .feedback-rating {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .feedback-rating>label {
    margin: 20px auto;
    /* margin-right: 5vw ; */
  }

}
/* footer css */
.footer-inner-body{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  gap: 40px;
  padding-bottom: 20px;
  padding-top: 20px;
  
  background-color: rgb(94, 94, 245);
}
.footer-map{
  width: 30vw;
  height: 15vw;
  min-width: 200px;
  min-height: 200px;
  box-shadow: 0px 0px 10px  black;
}
.footer-iframe{
  width: 100%;
  height: 100%;
}
.footer-text{
  width: 30vw;
  word-wrap: break-word;
}

@media screen and (max-width:500px){
  .footer-inner-body{
    flex-direction: column;
  }
  .footer-map{
    width: 90%;
    height: 70vw;
    min-width: 200px;
    min-height: 200px;
  }
  .footer-text{
    width: 70%;
    word-wrap: break-word;
  }
}
